<template>
    <div>
        <h3 class="LH-h3">Review Process</h3>
        <p class="LH-p">
            IJEP adopts an international review standard which lays emphasis on research groundwork, novelty, presentation, and format. Papers that authors submit to IJEP must be original work, never published earlier in any medium, and not currently under consideration for publication elsewhere. It also focuses on whether the paper is based on original research, as well as creativity of research method and rationality of conclusion.
        </p>

        <p class="LH-p" style="margin-top: 20px;">
            The paper processing chart is as follows.
        </p>

        <div class="reiew-img-box">
            <img src="../../assets/Review-Process.jpg" alt="">
        </div>

    </div>
</template>

<script>
    export default {
        name: "ReiewProcess",
        components: {
            
        },
        data() {
            return {
                
            };
        },
    };
</script>

<style scoped>

.reiew-img-box {
    margin: 10px auto 0 auto;
    text-align: center;
}
</style>



